body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


/* css variables */
:root{
  --theme-color:#282c34;
  --theme-color-contrast:#FFFFFF;
}

body{
  background: #fafafa;
  padding-top: 8vh;

}



.app-header {
  background-color:var(--theme-color);
  height: 7vh;
  display: flex;
  flex-direction: row;
  align-items: center;
  color: var(--theme-color-contrast);
  padding: 20px;
  position: fixed;
  width: 100%;
  top: 0;
  z-index: 5;
}

.app-header img {
  width: 40px;
  height: 40px;
  border-radius: 50%;
}

.app-header h4 {
  margin-left: 10px;
}

.nav .nav-each {
  background: transparent;
  color: var(--theme-color);
  font-weight: 400;
  border: 0.5px solid var(--theme-color);
  border-radius: 5px;
  margin: 10px 20px;
  padding: 5px 20px;
  text-decoration: none;
  display: inline-block;
  min-width: 100px;
}

.nav .nav-each:hover {
  box-shadow: 1px 1px;
}

.nav .nav-each-active {
  background: var(--theme-color);
  color: var(--theme-color-contrast);
}




.shortImg{
height: 80px;
}

.cardBody{
    text-align: center;
    padding: 0.25rem;
}


.form {
  position: relative;
  width: 100%;
  margin: 20px 0px 10px 0px;
}

.searchInput {
  height: 40px;
  width: 25%;
  margin: auto;
  transition: width 0.5s;
}

.searchInput:focus {
  width: 50%;
}

.searchResults {
  list-style-type: none;
  position: absolute;
  padding: 0;
  margin: 0;
  width: 50%;
  top: 40px;
  left: 25%;
  border-top: 1px solid var(--theme-color);
  border-radius: 0 0 2px 2px;
  box-shadow: 5px 8px 10px grey;
  background-color: white;
  z-index: 10;
}

.searchResultsShow {
  display: block;
}

.searchResultsHide {
  display: none;
}

.searchResults .searchResultItem {
  padding: 4px 8px;
  cursor: pointer;
}

li.searchResultItem:hover {
  background-color: #e3e3e3;
}

.hoveredSearchResultItem {
  background-color: #e3e3e3;
}

/* SORT FILTER STYLING */
.sort-filter {
  border-radius: 5px;
  border: 1px solid rgb(0, 0, 0, 0.1);
  padding: 15px 0px 0px 0px;
  box-shadow: 5px 5px 10px 0px #dae0e5;
}

.sort-filter .sort,
.sort-filter .filter {
  padding-left: 10px;
}

.sort-filter .sort h5,
.sort-filter .filter h5 {
  display: inline-block;
  font-size: 25px;
  font-weight: bold;
}

.sort-filter .sort select {
  margin-left: 10px;
}

.sort-filter .filter label {
  display: block;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.sort-filter .filter label:hover {
  color: #0062cc ;
}

/* AVENGER LIST STYLING */
.avenger-list ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
.avenger-list ul li {
  width: 100%;
  padding: 5px;
  cursor: pointer;
  border-radius: 5px;
}
.avenger-list ul li:hover {
  background: #dae0e569  !important;
}

.avenger-list ul li img {
  height: 50px;
  width: 50px;
  border: 2px solid black;
  border-radius: 50%;
  float: left;
  margin: 0px 10px 0px 0px;
}

.avenger-list ul li h5 {
  padding: 0;
  margin: 0;
}

.avenger-detail p,
div {
  margin-top: 0.35rem;
  margin-bottom: 0;
}

.avenger-detail p label {
  cursor: pointer;
}

.avenger-detail div {
  margin-top: 0.35rem;
  margin-bottom: 0;
}

.avenger-detail div ul {
  width: 80%;
  margin-top: 2px;
  border: 1px solid rgb(0, 0, 0, 0.1);
  box-shadow: 5px 5px 10px #d3d3d3;
  overflow: auto;
  max-height: 180px;
  list-style-type: decimal;
}

.avenger-detail div ul li {
  padding: 1px;
}

form.add {
  justify-content: center;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
}

.form-field {
  /* height: 30px; */
  width: 40%;
  margin: 5px 10px;  
  align-content: center;
}

label.form-field {
  font-weight: 500;
}

.form-field[type="text"],
.form-field[type="textarea"] {
  padding: 2px;
}

.gender {
  display: flex;
  justify-content: space-between;
}

.gender div {
  display: inline-block;
  width: 50%;
}
input[type="radio"],
input[type="checkbox"] {
  -webkit-transform: scale(1.5);
          transform: scale(1.5);
}
.gender div label {
  margin-left: 5px;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.movie-label{
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.form-field img{
  height: 80px;
   width: 80px;
   border: 2px solid black;
   border-radius: 50% ;
}
.cmp-property {
  display: inline;
}

.cmp-value {
  float: right;
  margin: 0;
}

.av-cmp-card {
  padding: 2px;
  border: 2px solid;
  width: 70%;
  margin: auto;
}

.av-cmp-card-winner {
  margin-top: 25px;
  border-color: green;
  box-shadow: 0px 10px 10px 0px;
  /* transform: scale(1.05); */
  -webkit-animation: winner-anim 2s infinite;
          animation: winner-anim 2s infinite;
}

@-webkit-keyframes winner-anim {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  50% {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes winner-anim {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  50% {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

.av-cmp-card-loser {
  border-color: red;
}

